@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';


@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap');

@keyframes popup {
  0% {
    opacity: 0;
    transform: scale(0.5);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

*,
::before,
::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
 

label {
  font-size: 1.2rem;
  color: #656262;
}

html,
body {
  height: 100%;
  width: 100%;
}

body {

  background: rgb(221, 221, 221);
  font-family: "Poppins", sans-serif;
}

.form_container {
  background-color: #ffffff;
  padding: 2rem 3rem; 
  border-radius: 0.5rem;
  width: 100%;
  max-width: 400px;
  position:absolute;
  left:45rem;
  top:9rem;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  
  
}

.form_container > h2 {
  font-family: 'oswald';
  font-weight: 400;
  margin-block: 1rem;
  padding-block: 0.6rem;
  color:  #283140;
}

.form_container > form {
  display: flex;
  flex-direction: column;
  gap: 1.4rem;
}

.form_container div {
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
}
.form_container label{
  font-family: 'oswald';
 
}
.form_container span{
  font-family: 'oswald';
 
}
.form_container input {
  font-family: 'oswald';
  color:#1b1b1b;
  border: none;
  padding: 0.5rem;
  border: 1px solid rgb(116, 116, 116);
  border-radius: 0.3rem;
  font-size: 1.1rem;
  outline: none;
}

.form_container input::placeholder {
  font-size: 0.9rem;
  font-style: italic;
}

.form_container button {
  background-color:  #283140;
  color: #fff;
  border: none;
  padding: 0.6rem;
  font-size: 1rem;
  cursor: pointer;
  border-radius: 0.3rem;
}

span a {
  text-decoration: none;
  color:  #283140;
  font-family: 'oswald';
}


 

